export default {
	data() {
		return {
			query: {
				username: null,
				password: null,
				phone: null,
				email: null
			}
		}
	},
	onLoad() {

	},
	methods: {
		// 提交注册信息
		async commitData(){
			let ret = await this.$http({
				url: "/register",
				method: 'POST',
				data: this.query
			})
			if (ret.code == 0){
				uni.showToast({
					title: ret.message
				})
				uni.navigateTo({
					url: '/pages/login/login'
				})
			}else {
				uni.showToast({
					icon: 'none',
					title: ret.message
				})
			}
		}
	}
}
